.navbar{
    width: 100%;
    height: 50px;
    position: absolute;
    z-index: 5;
    bottom: 0px;
    left: 0;
    background-color: white;
    transition: 1000ms;
    
    display: flex;
    align-items: center;

    /* padding: 0 40px; */
    padding: 0 10px;
    box-sizing: border-box;
    justify-content: space-between;
    
    width: 100%;
    z-index: 5;

}
.navbarTop{
    bottom: calc(100% - 50px);
}
.topNavButton{
    display: inline-block;
    text-decoration: none;
    color: #D65E4D;
    font-size: 22px;
    cursor: pointer;
    transition: 500ms;
    margin: 0 10px;
    position: relative;
}
.topNavButton img{
    width: 30px;
}
.topNav a, .topNavButton {
    color: #d65e4d;
    cursor: pointer;
    font-size: 22px;
    margin: 0 10px;
    text-decoration: none;
    transition: .5s;

}
.roundImage{
    border-radius: 50%;
    object-fit: cover;
    height: 30px;
    width: 30px;
}