/* 
  Z index values: 

  menuWindow 5
  navbar 5

  profileViewWindow 4

  filterBar 3

  searchResultsTopBar 2

*/

.App {
  height: 100%;
  width: 100%;
  background-color: rgb(255, 187, 40);
  position: absolute;
  top: 0;
  left: 0;
  box-sizing: border-box;
  transition: 500ms;
}
.appInner{
  height: 90%;
  width: 90%;
  position: absolute;
  bottom: 5%;
  left: 5%;
  background-color: rgba(128, 128, 128, 0.75);
  overflow: hidden;
  border: 2px solid black;
}
.appInner2{
  height: 100%;
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  background-color: rgba(128, 128, 128, 0.75);
  overflow: hidden;
}
.appScroller{
  height: 100%;
  width: 100%;
  overflow-y: auto;
}
img{
  height: 100%;
  width: 100%;
  object-fit: cover;
}
.spacer{
  height: 2000px;
  /* background-color: rgba(0, 0, 255, 0.411); */
  /* background-image: linear-gradient(rgb(87, 172, 66), rgb(142, 66, 187)); */
}
input {
  outline: none;
  border: none;
  padding: 8px 20px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.8) inset;    
  margin: 10px;
  vertical-align: top;
  box-sizing: border-box;
  width: calc(100% - 20px);
}
textarea {
  outline: none;
  border: none;
  padding: 5px 10px;
  box-sizing: border-box;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.8) inset;    
  margin: 10px;
  vertical-align: top;
}
select {
  outline: none;
  border: none;
  padding: 8px 20px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.8) inset;    
  margin: 10px;
  vertical-align: top;
  box-sizing: border-box;
  width: calc(100% - 20px);
}
button{
  width: 30%;
  padding: 5px 10px;
  background-color: rgb(243, 243, 243);
  background-image: linear-gradient(rgba(128, 128, 128, 0), rgba(201, 201, 201, 0.699));
  border-radius: 3px;
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.3);
  outline: none;
  border: none;
  margin: 10px;
  transition: 500ms;
  cursor: pointer;
}
button:hover{   
  background-color: rgb(250, 250, 250);     
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.3);
  outline: none;
  border: none;
  margin: 10px;
}
a{
  text-decoration: none;
  color: black;
}
pre{
  white-space: pre-wrap;
  word-wrap: break-word;
  text-align: left;
  max-width: 100%;
  margin: 0;
}
.checkBox{
  height: 25px;
  width: 25px;
  margin: 0;
}

.heightDisplay{
  width: 100%;
  text-align: center;
  position: absolute;
  top: 200px;
  background-color: orange;
}



.imageButtonPosition{
  position: absolute;
  top: 10px;
  right: 0px;
}
.imageButtonPosition2{
  position: absolute;
  top: 10px;
  right: 60px;    
}
.imageButtonSize{
  height: 50px;
  width: 50px;
  text-align: center;
  cursor: pointer;
  user-select: none;
}
.imageButtonImage{
  height: 40px;
  width: 40px;
  padding: 0px;
  display: inline-block;
}
.imageButtonImage2{
  height: 30px;
  width: 30px;
  padding: 0px;
  display: inline-block;
}
.imageButtonImage img{
  object-fit: contain;
  /* width: calc(100% - 10px); */
}
.imageButtonLabel{
  font-size: 8px;
  width: 100%;
  position: absolute;
  bottom: 5px;
}

.closeButton{
  position: absolute;
  top: 5px;
  right: 5px;
  height: 20px;
  width: 20px;
  background-color: rgb(243, 125, 125);
  border-radius: 3px;
  cursor: pointer;
  color: white;
  /* display: flex;
  justify-content: center;
  align-items: center; */
  transition: 500ms;
  line-height: 16px;
  z-index: 2;
  text-align: center;
}
.hidden{
  visibility: hidden;
  height: 0;
  width: 0;
  position: absolute;
}
.smallText{
  font-size: 8px;
}