.sideBar{
    display: inline-block;
    width: 40px;
    height: 100%;
    background-color: white;
    box-shadow: 0 0 10px black;
    position: absolute;
    left: 0;
    top: 0;
    transition: 1000ms;
    z-index: 2;
}
.sideBarRight{
    left: auto;
    right: 0;
}
.sideBarInner{
    height: calc(100% - 100px);
    overflow-y: auto;
    transition: 1000ms;
}
.sideBarButton{
    position: absolute;
    top: 5px;
    right: 5px;
    height: 30px;
    width: 30px;
    z-index: 3;
    font-size: 30px;
    line-height: 25px;
}
.sideBarButtonRight{
    /* right: auto; */
    /* left: 5px; */
}
.sideBarTitle{
    font-size: 20px;
    font-weight: 600;
    border-top: 1px solid black;
    border-bottom: 1px solid black;
    padding: 10px;
    text-align: center;
    transition: 1000ms;
}