.adminScreenView{
    height: 900px;
    width: 450px;
    border-radius: 10px;
    background-color: black;
    display: inline-block;
    overflow: hidden;
    padding: 5px;
    box-sizing: border-box;
    scale: 0.6;
    margin: -170px -75px;
}
.adminScreenViewInner{
    height: 100%;
    width: 100%;
    background-color: rgb(202, 213, 226);
    border-radius: 8px;
    position: relative;
    overflow: hidden;
}
.adminJsonView{
    display: inline-block;
    vertical-align: top;
    margin-top: 20px;
    margin-left: 20px;
}

/* Admin Page */
.adminPage{
    height: 100%;
    width: 100%;
    background-color: rgb(222, 229, 233);
}
.adminNotificationsBar{
    display: inline-block;
    width: 300px;
    height: 100%;
    background-color: white;
    box-shadow: 0 0 10px black;
    position: absolute;
    left: 0;
    top: 0;
}
.notificationBarInner{
    height: calc(100% - 120px);
    overflow-y: auto;
}
.adminNotification{
    border-bottom: 1px solid black;
    padding-top: 10px;
    text-align: center;
    position: relative;
}
.adminNotification button{
    width: calc(100% - 10px);
    margin: 10px 5px;
}
.notificationsBarDate{
    font-size: 14px;
    font-weight: normal;
}
.adminUserArea{
    padding: 20px 60px;
    padding-bottom: 60px;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    overflow-y: auto;
    background-color: rgb(202, 213, 226);
}
.adminUserDisplayScaler{
    scale: 0.5;
    margin:  -270px -145px;
    position: relative;
}
.adminUserDisplay{
    height: 680px;
    width: 600px;
    border-radius: 5px;
    border: 1px solid black;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.459);
    display: inline-block;
    position: relative;
    background-color: white;
    margin: 10px;
}
.adminUserDisplayHalf{
    display: inline-block;
    width: 50%;
    height: 100%;
    height: 560px;
    vertical-align: top;
    position: relative;
}
.adminUserNoteArea{
    height: 120px;
    width: 100%;
    background-color: white;
    padding: 5px;
    box-sizing: border-box;
}
.adminTileOpenInWindow{
    position: absolute;
    right: 40px;
    top: 5px;
    height: 30px;
    width: 30px;
    z-index: 2;
}
.notificationBarTop{
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 40px;
    padding: 20px 10px 40px 10px;
    box-sizing: border-box;
    border-bottom: 1px solid rgb(221, 221, 221);
}
.notificationBarTopItem{
    margin: 0 5px;
    display: inline-block;
}
.adminPageRedirect{
    height: 100%;
    width: 100%;
    background-color: rgb(235, 235, 235);
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center

}
.adminPageRedirectBox{
    /* height: 200px; */
    background-color: white;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.5);
    display: inline-block;
    padding: 20px;
    border-radius: 5px;
}
.adminPageRedirectButtons{
    padding-top: 40px;
}